<script>
export default {
  components: {

  },
  computed: {

  },
  data: () => ({
    image: false,
    large: false
  }),
  methods: {
    onResize () {
      this.large = window.innerWidth > 1200
    }
  },
  mounted () {
    // Bind resize to cut off some stuff
    this.onResize()
    window.addEventListener('resize', this.onResize, { passive: true })

    // Monitor parent scrolling to this section
    this.$root.$on('onSlide', (origin, destination) => {
      if (destination.anchor === 'about') {
          // Animate first item
          setTimeout((instance) => {
            instance.image = true
          }, 600, this)
      } else {
        this.image = false
      }
    })
  },
  beforeDestroy () {
    if (typeof window === 'undefined') return
    window.removeEventListener('resize', this.onResize, { passive: true })
  }
}
</script>
<style scoped>
.container {
  max-width: 90%;
  margin-right: 200px;
}
.image {
  object-fit: cover; /* Ensure images cover the container */
  width:80%;
  max-width: 800px;
  margin-left: 50px;
  margin-right:auto;
}

.bg {
  animation:slide 12s ease-in-out alternate;
  background-image: linear-gradient(60deg, #505050 50%, #303030 50%);
  bottom:0;
  left:-50%;
  opacity:.5;
  position:fixed;
  right:-50%;
  top:0;
  z-index:-1;
}

.bg2 {
  animation-direction:alternate-reverse;
  animation-duration:6s;
}

.bg3 {
  animation-duration:6s;
}

@keyframes slide {
  0% {
    transform:translateX(-25%);
  }
  100% {
    transform:translateX(0%);
  }
}


@media (max-width: 768px) {
  h1 {
    font-size: 14px;
  }
  p {
    font-size: 12px;
  }
  h2 {
    font-size: 14px;
  }
  b {
    font-size: 13px;
  }
}

.scroller {
    position:absolute;
    width:60px;
    height:100x;
    bottom:50px;
    right:25%;
    left:45%;
    margin-left:-25px;
}

.scrollmouse {
	width: 50px;
	height: 90px;
	border: 4px solid #909090;
	border-radius: 60px;
	position: relative;
	&::before {
		content: '';
		width: 12px;
		height: 12px;
		position: absolute;
		top: 10px;
		left: 50%;
		transform: translateX(-50%);
		background-color: #909090;
		border-radius: 50%;
		opacity: 1;
		animation: wheel 2s infinite;
		-webkit-animation: wheel 2s infinite;
	}
}

@keyframes wheel {
	to {
		opacity: 0;
		top: 60px;
	}
}

@-webkit-keyframes wheel {
	to {
		opacity: 0;
		top: 60px;
	}
}
</style>
