<template>
    <v-container>
      <h1>MY LATEST WORK</h1>
      <p>Want to see more? Visit my <a href="/projects">Project Page</a> and take a scroll!</p>
      <v-row>
        <v-col  xl="4"
        lg="4"
        md="4"
        sm="3"
        xs="3">
            <ProjectCard project="crowe" />
        </v-col>
        <v-col xl="4"
        lg="4"
        md="4"
        sm="3"
        xs="3" v-if="large">
            <ProjectCard project="ept" />
        </v-col>
        <v-col xl="4"
        lg="4"
        md="4"
        sm="3"
        xs="3" v-if="large">
            <ProjectCard project="dinner" />
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
import ProjectCard from '@/components/projects/ProjectCard'
  
  export default {
    components: {
        ProjectCard
    },
    computed: {

    },
    data: () => ({
      large: false
    }),
    methods: {
        onResize () {
            this.large = window.innerWidth > 800
        }
    },
    async mounted () {
      // Bind resize to cut off some stuff
        this.onResize()
        window.addEventListener('resize', this.onResize, { passive: true })
    },
    beforeDestroy () {
        if (typeof window === 'undefined') return
        window.removeEventListener('resize', this.onResize, { passive: true })
    }
  }
  </script>
  
  <style scoped>
